.sign-up2-container1 {
    width: 100%;
    height: 800px;
    display: flex;
    position: relative;
    align-items: center;
    flex-direction: row;
    justify-content: center;
  }
  .sign-up2-max-width {
    height: 100%;
    display: flex;
    align-items: flex-start;
    flex-direction: row;
  }
  .sign-up2-container2 {
    width: 50%;
    height: 100%;
    display: flex;
    align-self: center;
    align-items: flex-start;
    flex-direction: row;
    justify-content: flex-start;
  }
  .sign-up2-sign-up-image {
    width: 100%;
    height: 100%;
  }
  .sign-up2-form-root {
    gap: var(--dl-space-space-unit);
    width: 50%;
    height: 100%;
    display: flex;
    overflow: hidden;
    align-items: center;
    flex-shrink: 0;
    justify-content: center;
  }
  .sign-up2-form1 {
    gap: var(--dl-space-space-twounits);
