    display: flex;
    align-items: flex-start;
    flex-direction: column;
    justify-content: flex-end;
  }
  .sign-up2-title-root {
    gap: var(--dl-space-space-halfunit);
    display: flex;
    align-items: flex-start;
    flex-direction: column;
  }
  .sign-up2-have-an-account-login1 {
    gap: var(--dl-space-space-halfunit);
    display: flex;
    align-items: flex-start;
  }
  .sign-up2-form2 {
    gap: var(--dl-space-space-unit);
    width: 100%;
    height: auto;
    display: flex;
    flex-direction: column;
  }
  .sign-up2-username {
    gap: var(--dl-space-space-halfunit);
    width: 100%;
    display: flex;
    align-items: flex-start;
    flex-shrink: 0;
    flex-direction: column;
  }
  .sign-up2-textinput1 {
    width: 100%;
    border-color: var(--dl-color-theme-primary1);
    background-color: transparent;
  }
  .sign-up2-email {
    gap: var(--dl-space-space-halfunit);
    width: 100%;
