    align-items: flex-start;
    flex-shrink: 0;
    flex-direction: column;
  }
  .sign-up2-textinput2 {
    width: 100%;
    border-color: var(--dl-color-theme-primary1);
    background-color: transparent;
  }
  .sign-up2-password {
    gap: var(--dl-space-space-halfunit);
    width: 100%;
    display: flex;
    align-items: flex-start;
    flex-direction: column;
  }
  .sign-up2-textfield {
    gap: var(--dl-space-space-halfunit);
    width: 100%;
    display: flex;
    align-items: flex-start;
    flex-shrink: 0;
    flex-direction: column;
  }
  .sign-up2-container3 {
    width: 100%;
    display: flex;
    align-items: center;
    flex-direction: row;
    justify-content: space-between;
