    flex-shrink: 1;
    justify-content: center;
  }
  .sign-up2-icon1 {
    fill: var(--dl-color-theme-neutral-dark);
    width: 24px;
    height: 24px;
  }
  .sign-up2-textinput3 {
    width: 100%;
    border-color: var(--dl-color-theme-primary1);
    background-color: transparent;
  }
  .sign-up2-text17 {
    font-size: 14px;
