  .sign-up2-text25 {
    display: inline-block;
  }
  @media(max-width: 991px) {
    .sign-up2-container2 {
      width: 100%;
      height: var(--dl-size-size-xxlarge);
    }
    .sign-up2-form-root {
      width: 100%;
    }
  }
  @media(max-width: 479px) {
    .sign-up2-button {
      width: 100%;
    }
  }
  